body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  color: #333;
}

.home {
  background-color: #fff;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.home img {
  width: 100%;
}

.section {
  padding: 2.5rem;
}
.page {
  padding: 2.5rem;
  min-height: 100vh;
}
.page-primary {
  background: -webkit-gradient(linear, left top, left bottom, from(#78d1dc), to(#4e75d7));
  background: -webkit-linear-gradient(#78d1dc, #4e75d7);
  background: -o-linear-gradient(#78d1dc, #4e75d7);
  background: linear-gradient(#78d1dc, #4e75d7);
}
.page-primary h1 {
  color: #fff;
  margin: 3rem 0 5rem 0;
}
.search {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  border-bottom: 3px solid rgba(255, 255, 255, 0.3);
  padding: 0.6rem 0;
  margin-bottom: 2.5rem;
}
.search img {
  width: 30px;
  margin-right: 1rem;
}
.search input {
  border: 0;
  background-color: transparent;
  font-size: 1.2rem;
  font-family: 'Montserrat', sans-serif;
}
.search input::-webkit-input-placeholder {
  color: #fff;
  font-size: 1.2rem;
}
.search input:-ms-input-placeholder {
  color: #fff;
  font-size: 1.2rem;
}
.search input::-ms-input-placeholder {
  color: #fff;
  font-size: 1.2rem;
}
.search input::placeholder {
  color: #fff;
  font-size: 1.2rem;
}

.list-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.chip {
  -webkit-box-shadow: 0 0 20px #ddd;
          box-shadow: 0 0 20px #ddd;
  padding: 0.6rem 1.3rem;
  border-radius: 2rem;
  color: #aaa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.chip > *:first-child {
  margin-right: 0.5rem;
}

p {
  font-size: 1.15rem;
  line-height: 1.5rem;
}
.desc {
  margin: 2rem 0;
}
.distance {
  font-weight: bold;
  color: #5f33d8;
  margin-bottom: 0.5rem;
}

.challenge-list {
  margin: -0.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 1rem;
}
.challenge-list .challenge-item {
  padding: 0.5rem;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.challenge-item > img {
  width: 100%;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}
.challenge-item > div {
  font-weight: bold;
}
.challenge-item {
  color: #333;
}
a {
  text-decoration: none;
}

.bottom-nav {
  background: -webkit-gradient(linear, right top, left top, from(#78d1dc), to(#4e75d7));
  background: -webkit-linear-gradient(right, #78d1dc, #4e75d7);
  background: -o-linear-gradient(right, #78d1dc, #4e75d7);
  background: linear-gradient(to left, #78d1dc, #4e75d7);
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.3rem 0;
}

.bottom-nav img {
  height: 25px;
}

.challenge-img {
  margin: 1rem 0;
  -webkit-box-shadow: 0 0 20px #aaa;
          box-shadow: 0 0 20px #aaa;
  width: 100%;
}

.challenge-desc {
  margin: 2rem -2.5rem 0;
  padding: 2.5rem 2.5rem 4.5rem 2.5rem;
  background: -webkit-gradient(linear, left top, left bottom, from(#78d1dc), to(#4e75d7));
  background: -webkit-linear-gradient(#78d1dc, #4e75d7);
  background: -o-linear-gradient(#78d1dc, #4e75d7);
  background: linear-gradient(#78d1dc, #4e75d7);
  color: #fff;
  min-height: 300px;
}

.challenge-desc p {
  padding-bottom: 2rem;
  border-bottom: 3px solid rgba(255, 255, 255, 0.3);
}
.info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.where,
.cryistals {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.where img {
  margin-right: 0.5rem;
}
.full {
  width: 100%;
}
.win {
  width: 100%;
  margin: 2rem 0 0rem 0;
}
.win-title {
  font-size: 2.5rem;
  text-align: center;
  line-height: 3rem;
}
.audio {
  opacity: 0;
}



